/* Tailwind Base Styles */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Dark Mode Styles */
html[data-theme='dark'] {
  @apply bg-gray-900 text-gray-100;
}

.bg-dark-bg {
  @apply bg-gray-900;
}

.text-light-text {
  @apply text-gray-100;
}

.border-dark-border {
  @apply border-gray-700;
}
