/* Import additional fonts from Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&family=Open+Sans:wght@400;600&display=swap');

/* Basic styling for the body */
body {
  background-color: #000; /* Black background for the entire app */
  color: #fff; /* White text color for better contrast */
  margin: 0;
  padding: 0;
  font-family: 'Calibri', 'Roboto', 'Open Sans', Arial, Impact, 'Haettenschweiler', 'Arial Narrow Bold' , sans-serif;
}

/* Tailwind dark mode setup (if using Tailwind CSS) */
@media (prefers-color-scheme: dark) {
  .dark {
    background-color: #000;
    color: #fff;
  }
}

/* Ensure all text elements are white */
* {
  color: #fff; /* White text color for all elements */
}

/* Optional: To ensure links and other interactive elements are visible */
a {
  color: #1e90ff; /* Use a contrasting color for links */
}

a:hover {
  color: #add8e6; /* Lighten link color on hover */
}
